.project-banner{
    margin: 20px;
    box-shadow: 0 0 5px black;
    border-radius: 5px;
    font-size: calc(10px + 1vmin);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: calc(25vw - 40px);
    min-width: 300px;
    min-height: 600px;
    height: 600px;
}

.project-banner .project-image{
    width: 100%;
    object-fit: cover;
    display: block;
}

.project-banner .project-info{
    padding: 20px;
    margin-bottom: 20px;
    overflow-y: auto;
}

.project-banner .project-link{
    margin: 20px;
    width: 200px;
    height: 40px;
    min-height: 40px;
    margin-top: auto;
}

.project-banner .project-button{
    width: 100%;
    height: 100%;
}