.homepage .homepage-header{
    background-image: url('../../../images/backgrounds/pexels-juan-pablo-serrano-arenas-1242348.jpg');
    box-shadow: 0 5px 5px rgb(126, 126, 126);
    min-height: 100vh;
    font-size: calc(10px + 2vmin);
    color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.homepage .homepage-header h1{
    font-size: calc(10px + 5vmin);
    margin: 20px;
}

.homepage .homepage-header p{
    font-size: calc(10px + 2vmin);
    margin: 10px;
}

.homepage-logo {
    margin: 20px;
    height: 40vmin;
    pointer-events: none;
}

.light-bg{
    margin: 0;
    padding: 0;
    background-color: white;
}

.dark-bg{
    margin: 0;
    padding: 0;
    background-color: rgb(250, 250, 250);
}

.info-segment {
    margin: 0 10%;
    padding: 20px;
    font-size: calc(10px + 1vmin);
}

@media (max-width: 800px) {
    .info-segment {
        margin: 0 0;
        padding: 20px;
        font-size: calc(10px + 1vmin);
    }
}

.intro{
    color: rgb(253, 193, 107);
}

.intro-description{
    font-size: calc(10px + 1.5vmin); 
}

.info-segment h1{
    text-align: center;
}

.skills-list{
    display: flex;
    flex-wrap: wrap;
}

.skill-panel{
    display: flex;
    background-color: rgb(221, 221, 221);
    border-radius: 5px;
    padding: 10px 20px;
    margin: 10px;
}

.skill-panel img{
    display: block;
    width: 25px;
    height: auto;
    margin: auto;
    padding-right: 10px;
}

.skills-table{
    display: flex;
    flex-wrap: wrap
}

.col {
    flex: 1;
    padding: 10px;
}

.col ul{
    display: block;
    list-style-type: disc;
}

.homepage-projects .button-container{
    text-align: center;
}

.homepage-projects .button-container button{
    margin: 40px auto;
    width: 50vw;
    min-width: 300px;
    max-width: 800px;
    height: 100px;
    font-size: 200%;
}

.sub-header{
    margin: 20px;
    text-align: center;
    font-size: calc(10px + 2vmin);
}
