.popup{
    animation: popup 0.75s;
}

.slide-in-from-left{
    animation: slide-in-from-left 0.5s;
}

.slide-in-from-right{
    animation: slide-in-from-right 0.5s;
}

.slide-in-from-up{
    animation: slide-in-from-up 0.5s;
}

.slide-in-from-down{
    animation: slide-in-from-down 0.5s;
}
  
@keyframes popup {
      0% {
          transform: scale(0, 0);
      }
      50% {
          transform: scale(1.1, 1.1);
      }
    80% {
          transform: scale(1, 1);
      }
}

@keyframes slide-in-from-left {
    from {
        transform: translateX(-100%);
    }
    to {
        transform: translateX(0);
    }
}

@keyframes slide-in-from-right {
    from {
        transform: translateX(+100%);
    }
    to {
        transform: translateX(0);
    }
}

@keyframes slide-in-from-up {
    from {
        transform: translateY(-100%);
    }
    to {
        transform: translateY(0);
    }
}

@keyframes slide-in-from-down {
    from {
        transform: translateY(+100%);
    }
    to {
        transform: translateY(0);
    }
}