.nav-container {
    display: flex;
    position: fixed;
    align-items: center;
    width: 100%;
    height: 60px;
    background-color: #ffffff;
    box-shadow: 0 3px 3px rgb(126, 126, 126);
    z-index: 10;
}

.nav-link{
    font-size: 1.5rem;
    margin: 0;
    padding: 1rem;
    text-decoration: none;
    color: inherit;
    color: black;
    display: flex;
}

.nav-link:hover {
    text-decoration: underline;
}

.logo{
    width: 50px;
    margin: 0;
    padding: 0;
    height: auto;
    object-fit: contain;
}