.timeline-container{
    padding: 20px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 1vmin);
}

.timeline-arrow{
    width: 5px;
    height: 200px;
    background-color: rgb(253,227,190);
    border-radius: 5px;
}

.timeline-arrow300{
    width: 5px;
    height: 300px;
    background-color: rgb(253,227,190);
}

.timeline-event{
    margin: 10px;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
}

.time-bubble{
    position: relative;
    border: 3px solid rgb(253,227,190);
    border-radius: 100%;
    text-align: center;
    padding: 10px;
    margin: auto;
}

.event-branch{
    max-width: 400px;
    position: absolute;
    background-color: rgb(253,227,190);
    border-radius: 5px;
    text-align: center;
    margin: auto 10px;
}

.event-branch img{
    width: 100%;
    object-fit: cover;
    display: block;
}

.branch-left{
    right: calc(50% + 30px);
}

.branch-right{
    left: calc(50% + 30px);
}

.event-branch h1{
    color: white;
    font-size: calc(10px + 1vmin);
}

.event-branch p{
    color: white;
    font-size: calc(10px + 0.5vmin);
}